import 'whatwg-fetch'
//create, update ,
// schema = { entityName:'', mainAlias:'', child:['cars', 'ff'], ref:['family']}

var example = {
  queryUrl: '',
  createUrl: '',
  updateUrl: '',
  deleteUrl: '',
  name: 'personEntity',
  alias: 'p',
  child: ['cars', 'fff'],
  ref: ['family']
}

var paramExample = {
  _pageNo: 2,
  _pageSize: 10,
  name: ''
}


class DataSource {

  constructor(schema) {
    this.schema = schema;
  }

  count(params) {
    //TODO
  }

  query(url, params) {
    //TODO
    var q = this._getQuery(params);
    q.set("_method", "get");
    return fetch(url, {method: 'POST', credentials: "same-origin", body: q});
  }

  _isRefKey(key) {
    if (this.schema.child) {
      for (var i = 0; i < this.schema.child.length; i++) {
        if (this.schema.child[i] == key) {
          return true;
        }
      }
    }
    return false;
  }

  _getQuery(params) {
    var q = new URLSearchParams();
    for (var key in params) {
      if (this._isRefKey(key)) {
        var cp = key + "_";
        for (var kk in params[key]) {
          q.append(cp + kk, params[key][kk]);
        }
      } else {
        var pvalue = params[key]
        if (pvalue instanceof Array) {
          for (var i = 0; i < pvalue.length; i++) {
            q.append(key, pvalue[i]);
          }
        } else {
          q.append(key, pvalue);
        }
      }
    }
    return q;
  }

  paged(params) {
    var q = this._getQuery(params);
    q.set("_method", "get");
    return fetch(this.schema.queryUrl || this.schema.url, {method: 'POST', credentials: "same-origin", body: q});
  }


  create(value, ext) {
    var data = new FormData();
    data.append(this.schema.name, JSON.stringify(value));
    if (ext) {
      for (var key in ext) {
        data.append(key, ext[key]);
      }
    }
    return fetch(this.schema.createUrl || this.schema.url, {method: 'POST', credentials: "same-origin", body: data});
  }

  update(value, params) {
    // debugger;
    var data = new FormData();
    data.append("_method", "put");
    data.append(this.schema.name, JSON.stringify(value));
    var pp = this._getQuery(params);
    for (var key of pp.keys()) {
      data.append(key, pp.get(key))
    }
    return fetch(this.schema.updateUrl || this.schema.url, {method: 'POST', credentials: "same-origin", body: data});
  }

  remove(params) {
    var q = this._getQuery(params);
    q.set("_method", "delete")
    return fetch(this.schema.deleteUrl || this.schema.url, {method: 'POST', credentials: "same-origin", body: q});
  }
  
  
}

export {DataSource}



