<style scoped>
  .layout{
    border: 1px solid #d7dde4;
    background: #f5f7f9;
    /*position: relative;*/
    border-radius: 4px;
    /*overflow: hidden;*/
    height: 1000px;
  }
  .layout-breadcrumb{
    padding: 10px 15px 0;
  }
  .layout-content{
    min-height: 200px;
    margin: 15px;
    overflow: hidden;
    background: #fff;
    border-radius: 4px;
  }
  .layout-content-main{
    padding: 10px;
  }
  .layout-copy{
    text-align: center;
    padding: 10px 0 20px;
    color: #9ea7b4;
  }
  .layout-menu-left{
    background: #464c5b;
  }
  .layout-header{
    height: 60px;
    background: #fff;
    box-shadow: 0 1px 1px rgba(0,0,0,.1);
  }
  .layout-logo-left{
    width: 90%;
    height: 30px;
    background: #5b6270;
    border-radius: 3px;
    margin: 15px auto;
  }
  .layout-ceiling-main a{
    color: #9ba7b5;
  }
  .layout-hide-text .layout-text{
    display: none;
  }
  .ivu-col{
    transition: width .2s ease-in-out;
  }

  .ivu-icon+a{
    color: rgb(158, 167, 180);
  }
  .ivu-row-flex {
    height: 100%;
  }
</style>
<template>
  <div class="layout" :class="{'layout-hide-text': spanLeft < 5}">
    <Row type="flex">
      <i-col :span="spanLeft" class="layout-menu-left">
        <Menu :active-name="activeMenu" theme="dark" width="auto">
          <div class="layout-logo-left">
            <Dropdown placement="bottom-start" trigger="click" style="margin: 5px 0px 0px 5px;"
                      @on-click="changeApp">
              <a href="javascript:void(0)" style="color: rgb(158, 167, 180);">
                应用: {{currentApp.name}}
                <Icon type="arrow-down-b"></Icon>
              </a>
              <Dropdown-menu slot="list">
                <Dropdown-item v-for="item of appArr" :name="item.id">{{item.name}}</Dropdown-item>
                <Dropdown-item divided>应用管理</Dropdown-item>
              </Dropdown-menu>
            </Dropdown>
          </div>
          <Menu-item name="appMgr">
            <Icon type="ios-keypad" :size="iconSize"></Icon>
            <a class="layout-text" href="appMgr.html">应用管理</a>
          </Menu-item>
          <Menu-item name="memberMgr">
            <Icon type="ios-keypad" :size="iconSize"></Icon>
            <a class="layout-text" :href="'memberMgr.html?appId='+currentApp.id">用户管理</a>
          </Menu-item>
          <Menu-item name="roleGroupMgr">
            <Icon type="ios-keypad" :size="iconSize"></Icon>
            <a class="layout-text" :href="'roleGroupMgr.html?appId='+currentApp.id">角色组管理</a>
          </Menu-item>
          <Menu-item name="roleMgr">
            <Icon type="ios-analytics" :size="iconSize"></Icon>
            <a class="layout-text" :href="'roleMgr.html?appId='+currentApp.id">角色管理</a>
          </Menu-item>
          <Menu-item name="appPropertiesMgr">
            <Icon type="ios-keypad" :size="iconSize"></Icon>
            <a class="layout-text" :href="'appPropertiesMgr.html?appId='+currentApp.id">应用参配</a>
          </Menu-item>
          <Menu-item name="pageMgr">
            <Icon type="ios-analytics" :size="iconSize"></Icon>
            <a class="layout-text" :href="'pageMgr.html?appId='+currentApp.id">页面管理</a>
          </Menu-item>
          <Menu-item name="apiMgr">
            <Icon type="ios-analytics" :size="iconSize"></Icon>
            <a class="layout-text" :href="'apiMgr.html?appId='+currentApp.id">API管理</a>
          </Menu-item>

        </Menu>
      </i-col>
      <i-col :span="spanRight">
        <div class="layout-header">
          <i-button type="text" @click="toggleClick">
            <Icon type="navicon" size="32"></Icon>
          </i-button>
        </div>
        <div class="layout-breadcrumb">
          <Breadcrumb>
            <Breadcrumb-item href="#">首页</Breadcrumb-item>
            <Breadcrumb-item href="#">应用中心</Breadcrumb-item>
            <Breadcrumb-item>某应用</Breadcrumb-item>
          </Breadcrumb>
        </div>
        <div class="layout-content">
          <div class="layout-content-main">
            <slot></slot>
          </div>
        </div>

      </i-col>
    </Row>
  </div>
</template>

<script type="text/ecmascript-6">
  import DataCenter from "../store/DataCenter.js";

  export default {
    props: ['activeMenu'],
    data () {
    return {
      currentApp: {name: "暂无应用"},
      appArr: [],
      spanLeft: 5,
      spanRight: 19,
      appList:[]
    }
  },
  computed: {
    iconSize () {
      return this.spanLeft === 5 ? 14 : 24;
    }
  },
  mounted: function(){
    this.ds = DataCenter.getDataSource("appMgr");
    let that = this;
    let urlAppId = DataCenter.getAppId();
    DataCenter.getPagedByDS(this.ds, function (data) {
      let appArr = [];
      let index = 0;
      for(let i= 0,len=data.result.length; i<len; i++) {
        let item = data.result[i];
        appArr.push({
          id: item.id,
          name: item.name
        });
        if(urlAppId == item.id) {
          index = i;
        }
      };
      that.appArr = appArr;
      if(appArr.length>0) {
        that.currentApp = appArr[index];
      }
    }, null, true);
  },
  methods: {
    changeApp: function(id) {
      var name;
      this.appArr.every(function (item, index, arry) {
        if(id == item.id) {
          name = item.name;
          return false;
        }
        return true;
      });

      this.currentApp = {
        id: id,
        name: name
      };
      location.href = location.pathname+"?appId="+id;
    },
    toggleClick () {
      if (this.spanLeft === 5) {
        this.spanLeft = 2;
        this.spanRight = 22;
      } else {
        this.spanLeft = 5;
        this.spanRight = 19;
      }
    }
  }
  }
</script>



