<template>
  <Form ref="formInline" :model="formInline" :rules="ruleInline">
    <Form-item prop="user">
      <Input type="text" v-model="formInline.user" placeholder="Username">
      <Icon type="ios-person-outline" slot="prepend"></Icon>
      </Input>
    </Form-item>
    <Form-item prop="password">
      <Input type="password" v-model="formInline.password" placeholder="Password">
      <Icon type="ios-locked-outline" slot="prepend"></Icon>
      </Input>
    </Form-item>
    <Form-item>
      <Button type="primary" @click="handleSubmit('formInline')">登录</Button>
    </Form-item>
  </Form>
</template>
<script type="text/ecmascript-6">
  import $ from 'jquery';

  import {DataSource} from '../../utils/dataSource';

  var ds = new DataSource({
    name: 'roleEntity',
    queryUrl: '/rest/configServer/access/roleMgr',
    createUrl: '/rest/configServer/access/roleMgr',
    updateUrl: '/rest/configServer/access/roleMgr',
    deleteUrl: '/rest/configServer/access/roleMgr'
  });


  export default {
    data () {
      return {
        formInline: {
          user: '',
          password: ''
        },
        ruleInline: {
          user: [
            {required: true, message: '请填写用户名11', trigger: 'blur'}
          ],
          password: [
            {required: true, message: '请填写密码', trigger: 'blur'}
          ]
        }
      }
    },
    methods: {
      async handleSubmit(name) {
//        await ds.remove({ids:[1111,222]});
//        await ds.paged({});
//    await ds.create({name:'xxx'})
//        await ds.update({name: 'xxxFFF', create: 1495518617000}, {id: '803724934734839808'})
//        return;
        var $this = this;
        this.$refs[name].validate(function (valid) {
          if (valid) {
//            $this.$Message.success('提交成功!');
            $.post('/login', {
              username: $this.formInline.user,
              password: $this.formInline.password,
              ajax: true
            }, function (data) {
              var result = JSON.parse(data);
              if (result.success) {
                window.location = 'index.html'
              } else {
                $this.$Message.error('username or password error.');
              }
            })
          } else {
            $this.$Message.error('表单验证失败!');
          }
        })
      }
    }
  }
</script>

<style>
</style>



