import {pageConfigs, dsConfigs} from "./PageConfigs.js";

import {DataSource} from "../utils/dataSource.js";

var apis = {};
function sendRequest(path, data, noNeedRoot) {
  return new Promise(function(resolve, reject) {
    if(true !== noNeedRoot) {
      path = serviceRootPath+path;
    }
    $.post(path, data, function(res){
      resolve(res);
    }, 'json');
  })

}
//校验手机号是否被注册过
apis.phoneRegisted = async function (phone) {
  let path = "register?_cnd=exists&_method=GET&_pageSize=20&_pageNo=1";
  let data = {
    "$phone": phone
  };
  return await sendRequest(path, data);
}
//新用户注册
apis.insertUser = async function (paramObj) {
  let path = "register?_method=POST";
  let data = {
    "$user": JSON.stringify(paramObj)
  };
  return await sendRequest(path, data);
}



apis.getPageConfig = function(pageName) {
  return pageConfigs[pageName];
}
apis.getDSConfig = function(dsName) {
  return dsConfigs[dsName];
}

apis.getDataSource = function(dsName) {
  return new DataSource(this.getDSConfig(dsName));
}
apis.getPagedByDS = async function(ds, callback, param, withoutApp) {
  if(!param) {
    param = {};
  }
  if(!withoutApp && this.getAppId) {
    param.appId = this.getAppId();
  }
  let response = await ds.paged(param);
  let data = await response.json();
  if(data.success) {
    callback && callback(data);
  }
  return data;
}
apis.getAppId = function() {
  return _getQueryString("appId");
}
function _getQueryString(name) {
  var reg = new RegExp("(^|&)" + name + "=([^&]*)(&|$)", "i");
  var r = window.location.search.substr(1).match(reg); //获取url中"?"符后的字符串并正则匹配
  var context = "";
  if (r != null)
    context = r[2];
  reg = null;
  r = null;
  return context == null || context == "" || context == "undefined" ? "" : context;
}
export default apis



